<template>
  <div class="index">
    <h1>todoList</h1>
    <div class="middle">
      <input type="text" v-model="todoTitle">
      <button @click="addData">添加</button>
    </div>

    <ul>
      <li v-for="item in clientData" :key="item.index" :class="{done: item.isDone}">
        <div id="top" @dblclick="handleShow">{{item.title}}</div>
        <span id="del" type="x" @click="deleteData(item._id)">x</span>
        <input type="checkbox" v-model="item.isDone"
               @change="doData(item._id,item.isDone)">
      </li>
    </ul>
  </div>
</template>

<script>
  import list from '../components/list'
  import axios from 'axios'

  export default {
    name: 'index',
    components:{
      list
    },
    data() {
      return {
        clientData: "",
        todoTitle: "",
      }
    },
    methods: {
      getData() {
        axios.get("/get").then(res => {
          // console.log(res.data.data);
          this.clientData = res.data.data;
          console.log(this.clientData);
        })
      },
      addData() {
        if (!this.todoTitle.trim ()) {
          alert("请输入内容")
        } else {
          axios.post("/add", {title:this.todoTitle}).then(res => {
            console.log(res.data);
            if(res.data.code==200){
              alert("添加成功")
              this.$router.go(0)
            }else{
              alert("添加失败")
            }
          })
        }
      },
      deleteData(id) {
        // console.log(id);
        axios.delete(`/delete/${id}`).then(res=>{
          console.log(res);
          if(res.data.code==200){
            alert("删除成功")
            this.$router.go(0)
          }else{
            alert("删除失败")
          }
        })
      },
      doData(id,value){
        let isDone = value?1:0;
        // console.log(value);
        axios.patch(`/update/${id}`,{isDone}).then(res => {
          console.log(res.data);
          if(res.data.code==200){
            alert("修改成功")
            this.$router.go(0)
          }else{
            alert("修改失败")
          }
        })
      },
      handleShow(index){
        let inp = document.querySelector("#inp");
        // inp.style.display = "block";

      }
    },
    mounted() {
      this.getData()
    }
  }
</script>

<style scoped lang="less">

  .index {
    width: 800px;
    margin: 0 auto;
    .middle {
      margin: 20px 0;
      input {
        font-size: 28px;
        width: 300px;
        height: 40px;
      }
      button {
        font-size: 18px;
        width: 70px;
        height: 40px;
      }
    }
  }
  h1 {
    margin: 50px 0;
    font-size: 36px;
  }
  ul {
    font-size: 22px;
    span[type="x"]{
      border: 1px solid #000;
      padding: 0 5px;
    }
    button{
      margin: 0 15px;
    }
    li {
      margin: 10px 0;
      position: relative;
      .inp{
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        width: 80px;
        z-index: 998;
        padding: 8px 0;
      }
      #top{
        width: 100px;
        float: left;
      }
      #del{
        width: 50px;
      }
    }
  }
  ul li.done{
      text-decoration: line-through;
  }
</style>



