<template>
  <div class="index">

    <ul>
      <li v-for="item in clientData" :key="item.index">{{item.title}}</li>
    </ul>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'list',
    props:{
      arr:{
        type:Array
      }
  },
    data() {
      return {
        clientData: "",
        todoTitle: ""
      }
    },
    methods: {
      getData() {
        axios.get("http://localhost:3000/get").then(res => {
          // console.log(res.data.data);
          this.clientData = res.data.data
        })
      },

    },
  }
</script>

<style scoped lang="less">
  .index {
    width: 800px;
    margin: 0 auto;
    .middle {
      margin: 20px 0;
      input {
        font-size: 28px;
        width: 300px;
        height: 40px;
      }
      button {
        font-size: 18px;
        width: 70px;
        height: 40px;
      }
    }
  }

  h1 {
    margin: 50px 0;
    font-size: 36px;
  }

  ul {
    font-size: 22px;
    li {
      margin: 10px 0;
    }
  }
</style>



